                  :options="boxes"
                  class="singleFull"
                />
              </b-input-group>
            </b-form-group>
          </b-col>
          <b-col cols="12">
            <b-form-group label="Tara cassetta" label-for="box_tare" label-cols-md="4">
              <b-input-group>
                <b-input-group-prepend is-text>
                  <feather-icon icon="GridIcon" />
                </b-input-group-prepend>
                <cleave
                  id="box_tare"
                  v-model="form.box_tare"
