        </b-col>
        <b-col cols="12">
          <b-form-group label="Peso merce" label-for="good_weight" label-cols-md="4">
            <b-input-group>
              <b-input-group-prepend is-text>
                <feather-icon icon="GridIcon" />
              </b-input-group-prepend>
              <cleave
                id="good_weight"
                v-model="form.good_weight"
                placeholder="Inserisci il peso della merce o pesala sulla bilancia"
                :options="number"
                value=""
                class="doubleFull cleave"
                numeric-only
              />
              <b-input-group-append is-text> Kg. </b-input-group-append>
            </b-input-group>
          </b-form-group>
        </b-col>
        <b-col cols="12">
          <b-form-group label="Categoria" label-for="good_category" label-cols-md="4">
            <b-input-group>
              <b-input-group-prepend is-text>
