<script>
import { FormWizard, TabContent } from "vue-form-wizard";
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue";
import vSelect from "vue-select";
import Cleave from "vue-cleave-component";
import Ripple from "vue-ripple-directive";

import "vue-form-wizard/dist/vue-form-wizard.min.css";

import {
  BRow,
  BCol,
  BFormGroup,
  BFormInput,
  BInputGroup,
  BInputGroupPrepend,
  BInputGroupAppend,
  BFormCheckbox,
  BForm,
  BCard,
  BCardTitle,
  BCardText,
  BCardHeader,
  BCardBody,
  BButton,
} from "bootstrap-vue";

export default {
  components: {
    FormWizard,
    TabContent,
    BRow,
    BCol,
    BInputGroup,
    BFormGroup,
    BFormInput,
    BInputGroupPrepend,
    BInputGroupAppend,
    BFormCheckbox,
    BForm,
    BButton,
    BCard,
    BCardTitle,
    BCardText,
    BCardHeader,
    BCardBody,
    vSelect,
    Cleave,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      number: {
        numeral: true,
        numeralThousandsGroupStyle: "thousand",
        autocomplete: "off",
      },
      form: {
        box_code: "",
        box_tare: this.$store.getters["defaults/GET"].boxTare,
        good_id: "",
        good_weight: "",
        good_category: "",
        good_origin: "",
        good_price: "",
      },
      countries: [],
      boxes: [],
      goods: [],
      goodCategories: [],
    };
  },
  beforeCreate() {
    const self = this;

    this.$store.dispatch("boxes/get").then((result) => {
      console.log(result);
      self.boxes = result.data.data;
    });

    this.$store.dispatch("goods/get").then((result) => {
      console.log(result);
      self.goods = result.data.data;
    });

    this.$store.dispatch("countries/get").then((result) => {
      console.log(result);
      self.countries = result.data.data;
    });

    this.$store.dispatch("goodcategories/get").then((result) => {
      console.log(result);
      self.goodCategories = result.data.data;
    });
  },
  setup() {
    const self = this;
    // console.log(this.$store);
  },
  created() {
    var self = this;
    window.$eventBus.$on("box", (data) => {      
      console.log("ðŸ‘‚ received box", data);
      self.form.box_code = data;
      self.form.box_tare = data.tare;
    });

    window.$eventBus.$on("weight", (data) => {      
      console.log("ðŸ‘‚ received weight", data);

      var tare = 0;
      if(self.form.box_tare){
        tare = self.form.box_tare;
      }
      self.form.good_weight = data - tare;
    });
  }, 
  destroyed() {
    console.log("â© destroyed");
    window.$eventBus.$off("weight");
    window.$eventBus.$off("box");
  },
  methods: {
    capitalize(text) {
      if (text === undefined) return "";
      return text
        .toLowerCase()
        .split(" ")
        .map((s) => s.charAt(0).toUpperCase() + s.substring(1))
        .join(" ");
    },
    formSubmitted() {
      const self = this;
      // console.log(self.form);

      const params = {};
      params.box_code = self.form.box_code.code;
      params.box_tare = self.form.box_tare;
      params.location = self.$store.getters["defaults/GET"].goodLocationAtCheckin;
      params.good_id = self.form.good_id.id;
      params.good_weight = self.form.good_weight;
      params.good_category = self.form.good_category.name;
      params.good_origin = self.form.good_origin.code;
      params.good_price = self.form.good_price;

      console.log("- create parameters", params);

      /* var updateParams = {};
      updateParams.code = self.form.box_code.code;
      updateParams.good_id = self.form.good_id.id;
      updateParams.good_weight = self.form.good_weight;
      updateParams.good_category = self.form.good_category.name;
      updateParams.good_origin = self.form.good_origin.code;
      updateParams.good_price = self.form.good_price;
      updateParams.good_origin = self.form.good_origin.code; */

      self.$store.dispatch("transactions/add", params).then((result1) => {
        console.log("- transaction added", result1);
        self.$store.dispatch("flows/add", params).then((result2) => {
          console.log("- flow added", result2);
          self.dataSaved();
        });
      });
    },
    dataSaved() {
      const self = this;
      /* self.$toast({
          component: ToastificationContent,
          props: {
            title: "Dati inseriti",
            icon: "EditIcon",
            variant: "success",
          },
        }); */
      this.$swal({
        title: "Check-in completato",
        text: "Vuoi fare il check-in con la stessa merce?",
        icon: "success",
        showCancelButton: true,
        confirmButtonText: "Si",
        cancelButtonText: "No, torna alla home",
        customClass: {
          confirmButton: "btn btn-primary",
          cancelButton: "btn btn-outline-danger ml-1",
        },
        buttonsStyling: false,
      }).then((result) => {
        if (result.value) {
          console.log("check-in");
          self.form.box_code = {};
          self.form.good_weight = "";
          this.$refs.checkinFormWizard.changeTab(2, 0);
        } else {
          this.$router.push({ name: "home" });
        }
      });
    },
  },
};
</script>

